import { useEffect, useRef, useState } from "react"
import { useSelector } from "react-redux"
import { NavLink, useParams } from "react-router-dom"
import { ToyChat } from "../cmps/toy-chat"
import { reviewService } from "../services/review.service"
import { socketService,SOCKET_EMIT_SET_TOY } from "../services/socket.service"
import { toyService } from "../services/toy.service"

export function ToyDetails() {
    const [toy, setToy] = useState(null)
    const [review, setReview] = useState('')
    const filter = useRef(reviewService.getDefaultFilter())
    const [reviews, setReviews] = useState(null)
    const { toyId } = useParams()

    useEffect(() => {
        loadToy()
        loadReviews()
        socketService.emit(SOCKET_EMIT_SET_TOY, toyId)
    }, [])

    async function loadToy() {
        try {
            const currToy = await toyService.getById(toyId)
            setToy(currToy)
        } catch (err) {
            console.log('Had issues in toy details', err)
        }
    }

    async function loadReviews() {
        try {
            const reviews = await reviewService.query({ ...filter.current, toyId })
            setReviews(reviews)
        } catch (err) {
            console.log('Had issues in toy reviews', err)
        }
    }

    function getTime(timeStamp) {
        return padWithLeadingZeros(new Date(timeStamp).getDay(), 2) + '/' + padWithLeadingZeros(((new Date(timeStamp).getMonth()) + 1), 2) + '/' + new Date(timeStamp).getFullYear() + '/' + ' at ' + padWithLeadingZeros(new Date(timeStamp).getHours(), 2) + ':' + padWithLeadingZeros(new Date(timeStamp).getMinutes(), 2)


    }
    function padWithLeadingZeros(num, totalLength) {
        return String(num).padStart(totalLength, '0');
    }


    function onChangeReview({ target }) {
        const { value } = target
        setReview(value)
    }

    async function onAddReview(ev) {
        ev.preventDefault()
        await reviewService.add(review, toy._id)
        loadReviews()
        setReview('')
    }
    if (!toy) return <div>Loading...</div>
    return (
        <section className="toy-details">
            <NavLink className='btn btn-back' to="/toy">Back</NavLink>
            <h1><span>Name</span> : {toy.name}</h1>
            <h2><span>Price</span> : {toy.price}$</h2>
            <h3><span>Labels</span> : {toy.labels.join(',')}</h3>
            <img src={`https://robohash.org/${toy.name}?set=set3`} />
            <p>In stock - {toy.inStock ? "Yes" : "No"}</p>
            {toy.owner && <p>This toy was made by {toy.owner.fullname}<br></br> at {getTime(toy.createdAt)}</p>}
            <div className="review-msg-area">
                <ToyChat toy={toy} toyId={toyId} />
                <div className="toy-reviews">
                    <h2>Reviews</h2>
                    {
                        reviews && <div className="reviews">
                            {reviews.map((review, idx) => <div key={idx} className="review">
                                <p>By : {review.byUser.fullname}</p>
                                <h3>{review.txt}</h3>
                            </div>)}
                        </div>
                    }
                    <form >
                        <textarea
                            type='text'
                            placeholder="your review"
                            value={review}
                            onChange={onChangeReview}
                            name='txt'
                        />
                        <button onClick={onAddReview}>Add review</button>
                    </form>
                </div>
            </div>


        </section>)
}