import { useEffect, useState } from "react"
import { useSelector } from "react-redux"
import { NavLink, useNavigate, useParams } from "react-router-dom"
import { showErrorMsg, showSuccessMsg } from "../services/event-bus.service"
import { toyService } from "../services/toy.service"
import { loadToys, saveToy } from "../store/toy.actions"
import Select from 'react-select';


export function ToyEdit() {
    const toys = useSelector((storeState) => storeState.toyModule.toys)
    const [toy, setToy] = useState(toyService.getEmptyToy())
    const { toyId } = useParams()
    const [options, setOptions] = useState([])
    const [selectedOption, setSelectedOption] = useState([])
    let labels = []
    const navigate = useNavigate()

    useEffect(() => {
        loadToys()
    }, [])

    useEffect(() => {
        toys.forEach(toy => {
            toy.labels.map(label => {
                const idx = labels.findIndex(currLabel => currLabel === label)
                if (idx === -1) labels.push(label)
            })
        })
        const currOptions = labels.map(currLabel => ({ value: currLabel, label: currLabel }))
        setOptions(currOptions)
    }, [toys])


    useEffect(() => {
        if (toyId){
            loadToy()
           setSelectedOption(toy.labels)
        }
    }, [])

    async function loadToy() {
        try {
            const currToy = await toyService.getById(toyId)
            setToy(currToy)
        } catch (err) {
            console.log('Had issues in toy details', err)
        }
    }

    function handleChange({ target }) {
        if (target.type === 'checkbox') var { checked: value, name: field, type } = target
        else var { value, name: field, type } = target
        value = (type === 'number') ? +value : value
        setToy((prevToy) => ({ ...prevToy, [field]: value }))
    }

    async function onSaveToy(ev) {
        ev.preventDefault()
        console.log('toy:',toy)
        try {
            const currToy = await saveToy(toy)
            showSuccessMsg('Toy Saved')
            navigate('/toy')
        } catch (err) {
            console.log('Cannot save toy', err)
        }
    }

    function onSelectOption(options) {
        const newOps = selectedOption
        newOps.push(options[options.length - 1])
        setSelectedOption(newOps)
        setToy((prevToy) => ({ ...prevToy, labels : options.map(ops => ops.value) }))
    }

    
    return (
        <form className="toy-edit">
            <h1>Edit Your Toy</h1>
            <label>Toy name
                <input value={toy.name} onChange={handleChange} name="name" />
            </label>
            <br></br>
            <label>Toy Price
                <input type="number" value={toy.price} onChange={handleChange} name="price" />
            </label>
            <br></br>
            {/* <label>Add new label
                <input type="text" value={toy.newLabel || ''} onChange={handleChange} name="label" placeholder="label, label, label" />
            </label> */}
            <Select
                onChange={onSelectOption}
                options={options}
                isMulti={true}
                defaultValue={selectedOption}
            />
            <label>in Stock
                <input className="checked" type="checkbox" checked={toy.inStock} onChange={handleChange} name="inStock" />
            </label>
            <div className="edit-btns">
                <button onClick={onSaveToy}>Save</button>
                <NavLink className='btn' to="/toy">Back</NavLink>
            </div>
        </form>
    )
}
