
import { httpService } from './http.service.js'
import { userService } from './user.service.js'
import { utilService } from './util.service.js'

const BASE_URL = "toy"

export const toyService = {
    query,
    getById,
    save,
    remove,
    getEmptyToy,
    getDefaultFilter,
    getEmptyMsg,
    addMsg,
    deleteMsg
}


function query(filterBy = getDefaultFilter()) {
    const queryParams = `?name=${filterBy.name}&inStock=${filterBy.inStock}&label=${filterBy.label}&sort=${filterBy.sort}`
    return httpService.get(BASE_URL + '/' + queryParams)
}

function getById(toyId) {
    return httpService.get(BASE_URL + '/' + toyId)
}

function remove(toyId) {
    return httpService.delete(BASE_URL + '/' + toyId)
}

function save(toy) {
    if (toy._id) {
        return httpService.put(BASE_URL + '/' + toy._id, toy)
    } else {
        toy.owner = userService.getLoggedinUser()
        return httpService.post(BASE_URL, toy)
    }
}
function getEmptyToy() {
    return {
        "name": "",
        "price": "",
        "labels": [],
        "createdAt": Date.now(),
        "inStock": true
    }
}

function getDefaultFilter() {
    return { name: '', inStock: '', label: '', sort: 'old' }
}

function getEmptyMsg() {
    return {
        id: utilService.makeId(),
        txt: ''
    }
}

async function addMsg(toyId, msg) {
    try {
        console.log('msg:',msg)
        const savedMsg = await httpService.post(BASE_URL + '/' + toyId +'/msg', msg)
        return savedMsg
    } catch (err) {
       throw err
    }
}
async function deleteMsg(toyId, msgId) {
   await httpService.delete(BASE_URL + '/' + toyId +'/msg/'+ msgId)
}


