import { useEffect, useRef, useState } from "react"
import { useSelector } from "react-redux"
import { reviewService } from "../services/review.service"

export function UserProfile() {
    const user = useSelector((storeState) => storeState.userMoudle.user)
    const [reviews, setReviews] = useState(null)
    const filter = useRef(reviewService.getDefaultFilter())


    useEffect(() => {
        loadReviews()
    }, [])

    async function loadReviews() {
        try {
            const reviews = await reviewService.query({...filter.current , userId : user._id})
            setReviews(reviews)
        } catch (err) {
            console.log('Had issues in toy reviews', err)
        }
    }

    return (
        <main className="main-user">
            <h1>Hello , {user.fullname} </h1>
            <h2>Those are your reviews:</h2>
            <div className="user-reviews">
            {
                reviews && <div className="reviews">
                    {reviews.map((review , idx)=> <div key={idx} className="review">
                    <p>About : {review.aboutToy.name}</p>
                    <h3>{review.txt}</h3>
                    </div>
                    )}
                </div>
            }
            </div>
        </main>
    )
}