
import { useState } from 'react'
import { useSelector } from 'react-redux'
import { showErrorMsg, showSuccessMsg } from '../services/event-bus.service.js'
import { signup, login, logout } from '../store/user.action.js'


function getEmptyCredentials() {
    return {
        fullname: '',
        username: '',
        password: '',
    }
}

export function LoginSignup() {

    const [credentials, setCredentials] = useState(getEmptyCredentials())
    const [isSignupState, setIsSignupState] = useState(false)
    const user = useSelector((storeState) => storeState.userMoudle.user)


    function handleCredentialsChange({ target }) {
        const { name: field, value } = target
        setCredentials((prevCreds) => ({ ...prevCreds, [field]: value }))
    }

    async function onSubmit(ev) {
        ev.preventDefault()
        const func = isSignupState ? signup : login
        try {
            const user = await func(credentials)
            showSuccessMsg(`Welcome ${user.fullname}`)
        } catch (err) {
            showErrorMsg('OOps try again')
        }
    }

    function onToggleSignupState() {
        setIsSignupState(!isSignupState)
    }

    function onLogout(){
        logout()
    }



    const { username, password, fullname } = credentials
    if(!user)return <div className="login-page">

        <form className="login-form" onSubmit={onSubmit}>
            <input
                type="text"
                name="username"
                value={username}
                placeholder="Username"
                onChange={handleCredentialsChange}
                required
                autoFocus
            />

            <input
                type="password"
                name="password"
                value={password}
                placeholder="Password"
                onChange={handleCredentialsChange}
                required
            />

            {isSignupState && <input
                type="text"
                name="fullname"
                value={fullname}
                placeholder="Full name"
                onChange={handleCredentialsChange}
                required
            />}

            <button>{isSignupState ? 'Signup' : 'Login'}</button>
        </form>

        <div className="btns">
            <a href="#" onClick={onToggleSignupState}>
                {isSignupState ? 'Already a member? Login' : 'New user? Signup here'}
            </a >
        </div>
    </div >
    if(user) return (
        <div>
            <h1>Hello {user.fullname}</h1>
            <button onClick={onLogout}>Logout</button>
        </div>
    )
}


