import { useEffect, useState } from "react"
import { toyService } from "../services/toy.service"
import { socketService, SOCKET_EMIT_DELETE_MSG, SOCKET_EMIT_SEND_MSG, SOCKET_EMIT_TYPE_MSG, SOCKET_EMIT_UNTYPE_MSG, SOCKET_EVENT_ADD_MSG, SOCKET_EVENT_REMOVE_MSG, SOCKET_EVENT_TYPE_MSG, SOCKET_EVENT_UNTYPE_MSG } from "../services/socket.service"
import { showErrorMsg, showSuccessMsg } from "../services/event-bus.service"
import { useSelector } from "react-redux"



export function ToyChat({toy , toyId}) {
    const user = useSelector((storeState) => storeState.userMoudle.user)
    const [typing, setTyping] = useState(false)
    const [msg, setMsg] = useState(toyService.getEmptyMsg())
    const [msgs, setMsgs] = useState([])

    useEffect(() => {
        socketService.on(SOCKET_EVENT_ADD_MSG, addMsg)
        socketService.on(SOCKET_EVENT_REMOVE_MSG, deleteMsg)
        socketService.on(SOCKET_EVENT_TYPE_MSG, userTyping)
        socketService.on(SOCKET_EVENT_UNTYPE_MSG, userNotTyping)
        return () => {
            socketService.off(SOCKET_EVENT_ADD_MSG, addMsg)
            socketService.off(SOCKET_EVENT_REMOVE_MSG, deleteMsg)
            socketService.off(SOCKET_EVENT_TYPE_MSG, userTyping)
            socketService.off(SOCKET_EVENT_UNTYPE_MSG, userNotTyping)
        }
    }, [])

    useEffect(() => {
        setMsgs(toy.msgs)
    }, [])

    function addMsg(newMsg) {
        setMsgs(prevMsgs => [...prevMsgs, newMsg])
    }

    function userTyping(user) {
        const typingUser = {
            isTyping: true,
            user: user.fullname
        }
        setTyping(typingUser)
    }

    function userNotTyping() {
        setTyping(false)
    }

    function onChangeMsg({ target }) {
        const { value, name: field } = target
        if (value.length > 0) socketService.emit(SOCKET_EMIT_TYPE_MSG, user)
        else if (value.length === 0) socketService.emit(SOCKET_EMIT_UNTYPE_MSG)
        setMsg((prevMsg) => ({ ...prevMsg, [field]: value }))
    }

    async function onSendMsg(ev) {
        ev.preventDefault()
        try {
            socketService.emit(SOCKET_EMIT_UNTYPE_MSG)
            msg.by = user
            socketService.emit(SOCKET_EMIT_SEND_MSG, msg)
            setMsg(toyService.getEmptyMsg())
        } catch (err) {
            console.log('err', err)
            showErrorMsg('Cannot save Msg')
        }
    }

    function onDeleteMsg(msg) {
        if (user._id !== msg.by._id) {
            showErrorMsg('Cannot remove another user\'s msg')
        }
        else {
            try {
                console.log('msg.id:',msg.id)
                socketService.emit(SOCKET_EMIT_DELETE_MSG, msg.id)
            } catch (error) {
                showErrorMsg('Cannot remove msg')
            }
        }
    }

    async function deleteMsg(msgId) {
        try {
            const updatedMsgs = msgs.filter(msg => msg.id !== msgId)
            setMsgs(updatedMsgs)
            showSuccessMsg('Msg Removed!')
        } catch (err) {
            console.log('err:', err)
            showErrorMsg('Cannot remove Msg')
        }
    }

    return (
        <div className="toy-msgs">
            <h2>Messages</h2>
            {typing && <p>{typing.user} is typing...</p>}
            {
                msgs.length > 0 && <ul>
                    {msgs.map((msg, idx) => <li key={idx}>
                        <span> {msg.by._id === user._id ? 'Me :' : msg.by.fullname + ' :'}</span>{msg.txt}
                        <button onClick={() => onDeleteMsg(msg)}>X</button>
                    </li>)}
                </ul>
            }
            <form>
                <input
                    placeholder="your message"
                    value={msg.txt}
                    onChange={onChangeMsg}
                    name='txt'
                />
                <button onClick={onSendMsg}>Send</button>
            </form>
        </div>
    )
}