import { httpService } from './http.service.js'

const STORAGE_KEY_LOGGEDIN = 'loggedinUser'
const BASE_URL_USER = 'user/'
const BASE_URL_AUTH = 'auth/'

export const userService = {
    login,
    logout,
    signup,
    getById,
    getLoggedinUser
}

window.us = userService

function getById(userId) {
    return httpService.get(BASE_URL_USER + userId)
}

async function login(credentials) {
    try {
        const user = await httpService.post(BASE_URL_AUTH + 'login', credentials)
        _setLoggedinUser(user)
        return user
    } catch (err) {
        console.log('err:', err)
        throw new Error('Invalid login')
    }
}


async function signup({ username, password, fullname }) {
    const user = { username, password, fullname }
    try {
        const newUser = await httpService.post(BASE_URL_AUTH + 'signup', user)
        _setLoggedinUser(newUser)
        return newUser
    } catch (err) {
        console.log('err:', err)
        throw new Error('Invalid signup')
    }
}

function logout() {
    return httpService.post(BASE_URL_AUTH + 'logout')
        .then(() => {
            sessionStorage.removeItem(STORAGE_KEY_LOGGEDIN)
        })
}

function getLoggedinUser() {
    // return httpService.get(BASE_URL + userId)
    const user = JSON.parse(sessionStorage.getItem(STORAGE_KEY_LOGGEDIN))
    console.log('user:',user)
    return user
}

function _setLoggedinUser(user) {
    const userToSave = { _id: user._id, fullname: user.fullname , isAdmin : user.isAdmin }
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN, JSON.stringify(userToSave))
    return userToSave
}




